"use strict";

var extend = require('extend');

var _native = require("./native");

var funcRegex = new RegExp("^[a-zA-Z$][A-Za-z0-9_$]*$");

var NVM = function NVM(block, transaction) {
  extend(_native.context.block, block);
  extend(_native.context.transaction, transaction); // console.log("block:", native.context.block);
  // console.log("tx:", native.context.transaction);
};

NVM.prototype = {
  deploy: function deploy(source, args) {
    return this.run(source, "init", args);
  },
  call: function call(source, func, args) {
    if (funcRegex.test(func)) {
      return this.run(source, func, args);
    } else {
      throw new Error("invalid func");
    }
  },
  run: function run(source, func, args) {
    Blockchain.blockParse(JSON.stringify(_native.context.block));
    Blockchain.transactionParse(JSON.stringify(_native.context.transaction));
    var Contract = eval(source); // console.log("contract:", Contract);

    var contract = new Contract();

    if (args === undefined || args.length === 0) {
      args = "[]";
    }

    if (contract[func] != undefined) {
      return contract[func].apply(contract, JSON.parse(args));
    } else {
      throw new Error("function not found");
    }
  }
};
module.exports = NVM;